import React from "react";
import * as uuid from "uuid";
import { createBrowserHistory } from "history";
import RegisterForm from "../components/users/RegisterForm";
import Users from "../models/Users";
import Api from "../services/Api";
const api = new Api();
const history   = createBrowserHistory({forceRefresh:true});

class  Register extends React.Component{
    constructor(props){
        super(props);
        this.state={
            id : uuid.v4(),
            email: '',
            password : '',
            isAdmin:'false',
            avatar:'avatar2.jpg',
            confirmPassword:'',
            users: [],
            btnSubmit:false,
            linkTo:"",
        }
    }

    setId=(event)=>{
        event.preventDefault();
        this.setState({ id: event.target.value});
    }

    setEmail=(event)=>{
        event.preventDefault();
        this.setState({ email : event.target.value });
    }

    setAvatar=(event)=>{
        event.preventDefault();
        this.setState({ avatar : event.target.value });
    }
    setIsAdmin=(event)=>{
        event.preventDefault();
        this.setState({ isAdmin : event.target.value });
    }
    setPassword=(event)=>{
        event.preventDefault();
        this.setState({ password: event.target.value });
    }

    setConfirmPassword=(event)=>{
        event.preventDefault();
        this.setState({ confirmPassword: event.target.value });
    }

    onSubmit = async (event)=>{
        event.preventDefault();
        let user = {
            id: this.state.id,
            email: this.state.email,
            isAdmin: this.state.isAdmin,
            avatar: this.state.avatar,
            password: this.state.password,
        }
        if(this.state.password===""){
            return alert(" Password is empty");
        }
        if(this.state.confirmPassword ===""){
            return alert("Password Confirmation is Empty");
        }
        if(this.state.confirmPassword !== user.password){
            return alert(" Password confirmation is not equal with Password");
        }

        const newUser = new Users(user.id, user.email, user.isAdmin, user.avatar, user.password);
        
        await api.create(newUser)
        .then(( res)=> res.json())
        .then((data)=>{
           
            this.setState({ users: data, btnSubmit: true, linkTo:""});
        })
        .catch((error)=>{
            console.log({ message: error.message });
        })  

        alert("ID :"+newUser.id+ " inserted successfully !");
        this.setState({ id: '', email: '', password: ''});
        history.push('/react-login'); 
        window.location.reload();
    }

    render(){
        return(
            <React.Fragment>
                <RegisterForm 
                    id={this.state.id}
                    setId={this.setId}
                    email={this.state.email}
                    setEmail={this.setEmail}
                    avatar={this.state.avatar}
                    setAvatar={this.setAvatar}
                    isAdmin = {this.state.isAdmin}
                    setIsAdmin = {this.setIsAdmin}
                    password = {this.state.password}
                    setPassword={this.setPassword}
                    confirmPassword = {this.state.confirmPassword}
                    setConfirmPassword = {this.setConfirmPassword}
                    onSubmit={this.onSubmit}                   
                    linkTo={this.state.linkTo}
                />
            </React.Fragment>
        )
    }
}
export default Register;