import React, { Component, Fragment } from "react";
import { BrowserRouter, Routes, Route  } from 'react-router-dom';
import Header from "../src/components/Header";
import Footer  from "../src/components/Footer";
import Home from "../src/pages/Home";
import Register from "../src/pages/Register";
//import Profile from "./pages/Profile";

class App extends Component {
    constructor(){
        super();
        this.state = {
            currentYear : "1900",
            authorLabel: "Bedson Gultom. All rights reserved",
           // isExpand : false,
        }
        //this.toggleNavbar.bind(this);
    }

    componentDidMount =()=>{
        this.getCurrentYear();
    }
    getCurrentYear =()=>{
        const date = new Date();
        const currentYear = date.getFullYear();
        return this.setState({ currentYear: currentYear});
    }
   
    /*
    toggleNavbar = ()=>{		
        console.log({Log: !this.state.isExpand ? 'Navbar is Opened': 'Navbar is Closed'});
        this.setState({isExpand: !this.state.isExpand});
    }
    */

    render(){
        const { isExpand } = this.state;
        return(
            <Fragment>                
                <BrowserRouter>
                    <Header />{/*onClick= {this.toggleNavbar} isExpand={ !isExpand }*/}
                        <Routes>                          
                            <Route path="/react-login" element={<Home/>}/>
                           {/* <Route path="/react-login/profile" element={<Profile/>}/>*/}
                            <Route path="/react-login/register" element={<Register/>}/>
                        </Routes>

                    <Footer currentYear={" 2020-" + this.state.currentYear} author={this.state.authorLabel} />
                </BrowserRouter>
            </Fragment>    
        )
    }
}
export default App;




