import React, { Component, Fragment} from "react";
import 'bootstrap/dist/css/bootstrap.css';

class Footer extends Component{
    render(){
        return(
           <Fragment>
                <footer className="footer">
                    <div className="bg-warning text-center p-3 fixed-bottom">
                        <label className="text-white"> { this.props.currentYear } | </label>
                        <span className="text-white"> {this.props.author} </span>
                    </div>
                </footer>
            </Fragment>
        )
    }
}
export default Footer;