import React from "react";
import { Link } from "react-router-dom";

class LoginForm extends React.Component{
    render(){
        return(
            <React.Fragment>
                <div className="page-wrap">
                    <div className="page-center">
                        <div className="card p-5 border-gray rounded-5 border-1">
                            <form onSubmit={this.props.onSubmit}>
                                <div className="form-group">
                                    <label htmlFor="email" className="text-muted">Email</label>
                                    <input type="text" name="email"
                                        className="form-control border-gray rounded-5 border-1 " 
                                        placeholder="Enter Email ..."
                                        value={this.props.email}
                                        onChange={this.props.setEmail}
                                    />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="password" className="text-muted">Password</label>
                                    <input type="password" name="password" 
                                        className="form-control border-gray rounded-5 border-1" 
                                        placeholder="Enter Password ..."
                                        value={this.props.password}
                                        onChange={this.props.setPassword}
                                    />
                                </div>
                                <div className="form-group pt-3">
                                    <button className="btn btn-sm btn-primary border-gray rounded-5 border-1"
                                        type="submit"> Login
                                    </button>
                                </div>
                                <hr/>
                                <div className="form-group">
                                    <label className="ps-0 text-muted">Don't Have an Account ?<Link to="/react-login/register"> Register</Link> </label>
                                </div>           
                            </form>
                           </div> 
                           </div>

                        </div>
            </React.Fragment>
        )
    }

}

export default LoginForm;