class Api {
    get = async ()=>{
        return  await fetch("https://json-server-db.vercel.app/users", {
            method: "GET",
            headers: {
                "Content-Type": "application/json",
                "Accept":"application/json",
                //handling error 304 on browser
                "Cache-Control":"no-cache, no-store",
                "Pragma": "no-cache",
                "Expires": 0,
            },
        });
    }
    
    create = async(state=[])=>{
        return await fetch("https://json-server-db.vercel.app/users", {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                "Accept":"application/json"
                
            },
            body:JSON.stringify({id: state.id, email: state.email, isAdmin: state.isAdmin, avatar: state.avatar,  password: state.password})
        })
    }

    signIn = async(state=[])=>{
        return await fetch("https://json-server-db.vercel.app/users?email="+state.email+"&password="+state.password,  {
            method: "GET",
            headers: {
                "Content-Type": "multipart/form-data",
                "Accept":"application/json"
            }
        })
    }


    getItems = async ()  =>{
        return  await fetch("https://json-server-db.vercel.app/items", {
            method: "GET",
            headers: {
                "Content-Type": "application/json",
                "Accept":"application/json",
                "Cache-Control":"no-cache, no-store",
                "Pragma": "no-cache",
            }
        })
    }

    getItemById = async(id)=>{
        return await fetch("https://json-server-db.vercel.app/items/"+ id, {
            method: "GET",
            headers: {
                "Content-Type": "application/json",
                "Accept":"application/json",
                //handling error 304 on browser
                "Cache-Control":"no-cache, no-store",
                "Pragma": "no-cache",
            },
        });
    }

    createNewItem = async(state=[])=>{
        return await fetch("https://json-server-db.vercel.app/items", {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                "Accept":"application/json",
            },
            body:JSON.stringify({id: state.id, item_name: state.item_name, image: state.image, price: state.price})
        })
    }

    updateItem = async(id, state=[])=>{
        return await fetch("https://json-server-db.vercel.app/items/" + id, {
            method: "PUT",
            headers: {
                "Content-Type": "application/json",
                "Accept":"application/json"
            },
            body:JSON.stringify({item_name: state.item_name, image:state.image,  price: state.price})
        })   
    }

    deleteItemById = async(id)=>{
        return await fetch("https://json-server-db.vercel.app/items/"+ id, {
            method: "DELETE",
            headers: {
                "Content-Type": "application/json",
                "Accept":"application/json",
                "Access-Control-Allow-Origin": "*",

            },
        })   
    }

}  
export default Api;
