import React from "react";

class ItemList extends React.Component{
    render() {
        return(
            <React.Fragment>
                <tr>
                    <td>{this.props.id}</td>
                    <td>{this.props.item_name}</td>                   
                    <td> <img src={"https://bedsongultom.com/images/"+this.props.image} alt="avatar" width="70%" height="70%" id="circle"/></td>
                    <td>{this.props.price}</td>
                    <td  className="text-center">
                        <div className="d-grid gap-2 d-md-flex">
                            <button className=" btn btn-sm btn-warning text-white"
                                type="button" 
                                onClick={()=>this.props.handleEditBy(this.props.id)}>
                               Edt
                            </button>       
                            <button className=" btn  btn-sm btn-danger text-white"
                                type="button"
                                onClick={(e)=>this.props.handleDeleteBy(e, this.props.id)} >
                                Del
                            </button>                                
                        </div>
                    </td>
                </tr>               
            </React.Fragment>
        )
    }
}
export default ItemList;