import React from "react";
import { createBrowserHistory } from "history";
import LoginForm from "../components/users/LoginForm";
import ProfileForm from "../components/users/ProfileForm";
import ItemForm from "../components/items/ItemForm";
import ItemList from "../components/items/ItemList";
import Api from "../services/Api";
import { isDisabled } from "@testing-library/user-event/dist/utils";
const api       = new Api();
const today     = new Date();
const history   = createBrowserHistory({forceRefresh:true});

class Home extends React.Component{
    constructor(){
        super();
        this.state ={
            id : '',
            email:'',
            password:'',
            item_name:'',
            image:'noImage.png',
            price:'',
            users:[],
            items:[],
            isLoggedIn: false, 
            currentDate:'',
            isOnclickItems: false,
            isOnclickBack: false,
            items:[{
                id:'',
                item_name:'',
                price:''
            }],  
            isDisabledBtnUpdate: true,
            isDisabledBtnInsert: false,
        }   
    }

    componentDidMount =()=>{
        this.getDateTime();
        this.readItems();
    }

    currencyFormatter=(money)=>{
        return new Intl.NumberFormat('id-ID', {style: 'currency', currency:'IDR', minimumFractionDigits: 0}).format(money);
    }

    getDateTime=()=>{
        let currentDate;
        let date  = today.getDate();
        let month = today.getMonth()+1;
        let year  = today.getFullYear();
        let hours = today.getHours();
        let minutes = today.getMinutes();
        let seconds = today.getSeconds();
        // eslint-disable-next-line no-unused-vars
        currentDate =  date+"-"+month+"-"+year+" "+hours+" : "+ minutes+" : "+ seconds ;
        this.setState({ currentDate : currentDate });
    }

    setEmail = (event) =>{
        event.preventDefault();
        this.setState({ email: event.target.value });
    }
    setPassword = (event) =>{
        event.preventDefault();
        this.setState({ password : event.target.value });
    }
    setId = (event)=>{   
        this.setState({ id: event.target.value });
    }
    setItemName = (event)=>{
        this.setState({ item_name : event.target.value });
    }
    setImage =(event)=>{
        this.setState({ image : event.target.value });
    }
    setPrice = (event)=>{
        this.setState({ price: event.target.value });
    }
    onClickItems = ()=>{
        this.setState({ isOnclickItems: true });
    }
    onClickBack  = ()=>{
        this.setState({ isOnclickBack : true});
        window.location.reload()
    }
    onSubmit = async (event)=>{
        event.preventDefault();
        let users={
            email: this.state.email,
            password: this.state.password,
        }
        
        await api.signIn(users)
        .then((res)=> res.json())
        .then((data)=>{
            let email    = data[0].email;
            let password = data[0].password; 
            console.log({email: email, password: password});
            localStorage.setItem('users',JSON.stringify(data)); 
            alert(" You are logged In") 
            history.push("/react-login");
            this.setState({ isLoggedIn : true });

        })
        .catch((error)=>{
            if(error.message){
                return alert("Your Email or Password is wrong!");
            }  
        })
    }
    logOut =()=>{
        localStorage.removeItem("users");
        history.push('/react-login'); 
        window.location.reload();
    }

    readItems = async()=>{
        await api.getItems()
        .then((res)=>res.json())
        .then((data)=>{
            this.setState({ items: data });
            return this;
        })
        .catch((error)=>{
            console.error({ message: error.message });
        }) 
    }

    readItemsById = async(id)=>{
        this.setState({isDisabledBtnUpdate: false, isDisabledBtnInsert: true })

        await api.getItemById(id)
        .then((res)=>res.json())
        .then((data)=>{
            this.setState({ 
                id: id, 
                item_name: data.item_name, 
                image: data.image,
                price: data.price }); 
            return this; 
        })
        .catch((error)=>{
            console.error({ message: error.message });
        }) 
    }

    removeItemsBy = async(event, id)=>{
        event.preventDefault();
        await api.deleteItemById(id)
        .then((res)=>res.json())
        .then((data)=>{
            this.setState({ items: data });
        })
        .catch((error)=>{
            console.log({ message : error.message });
        })
        this.readItems();
        alert("ID: " +id+ " deleted successFully !");
    }

    handleAddItem= async (event)=>{
        event.preventDefault();
        const newItem = {
            id: this.state.id,
            item_name: this.state.item_name,
            image:'noImage.png',
            price: this.state.price
        };
        if(newItem.id ===""){
            return alert(" ID is Required !")
        }
        if(newItem.item_name ===""){
            return alert(" Item Name is Required !")
        }
        if(newItem.price ===""){
            return alert("Price is Required !")
        }
        await api.createNewItem(newItem)
        .then((res)=>res.json())
        .then((data)=>{
            this.setState({ items: data });
        })
        .catch((error)=>{
            console.log({ message : error.message });
        })

        this.readItems();
        alert("ID: "+newItem.id + " created successFully !");
        this.setState({ id:"", item_name:"", image:"", price:""});
    }

    handleOnClickUpdate = async (event, id)=>{
        event.preventDefault();
        const newItem = {
            id: this.state.id,
            item_name: this.state.item_name,
            image: this.state.image,
            price: this.state.price,

        };
        
        if(newItem.id ===""){
            return alert(" ID is Required !")
        }
        if(newItem.item_name ===""){
            return alert(" Item Name is Required !")
        }
        if(newItem.price ===""){
            return alert("Price is Required !")
        }
        await api.updateItem(id, newItem)
        .then((res)=>res.json())
        .then((data)=>{
            this.setState({ items: data });
        })
        .catch((error)=>{
            console.log({ message : error.message });
        })

        this.readItems();
        alert("ID: "+newItem.id + " updated successFully !");
        this.setState({ id:"", item_name:"", image:"", price:"", isDisabledBtnInsert: false, isDisabledBtnUpdate: true });

    }
    
    render(){
        const { items, isOnclickItems, isOnclickBack } =this.state;
        let users = JSON.parse(localStorage.getItem("users"));
        return(
            <React.Fragment>
                <div className="py-5">
                    { users===null &&
                        <LoginForm
                            email={this.state.email}
                            setEmail = {this.setEmail}
                            password ={ this.state.password}
                            setPassword = {this.setPassword}
                            onSubmit = {this.onSubmit}
                        /> 
                    }
            
                    { users!==null &&
                        !isOnclickItems &&
                        <ProfileForm email={users[0].email.toUpperCase()}
                            avatar= {users[0].avatar}
                            id={users[0].id} 
                            onClick={this.logOut}
                            onClickItems = {this.onClickItems} 
                            currentDate={this.state.currentDate}
                        />  
                    }
                </div>  

                { isOnclickItems &&
                    !isOnclickBack &&                    
                    <div className="page-wrap">
                        <div className="container  py-4">
                            <div className="pe-5 ps-5 pt-2 ">
                                <button type="submit" className="btn btn-sm btn-warning border-warning rounded-2 border-1 text-white float-end"
                                    onClick = {this.onClickBack}>
                                    Back
                                </button>
                            </div>
                            
                            <div className="p-5 border-gray rounded-0 border-1">
                            <hr/>
                                <ItemForm
                                    id={this.state.id} 
                                    item_name={this.state.item_name} 
                                    image = {this.state.image}
                                    price={this.state.price} 
                                    setId={this.setId}
                                    setItemName = {this.setItemName}                                    
                                    setImage = {this.setImage}
                                    setPrice = {this.setPrice}                                   
                                    handleAddItem={(e)=>this.handleAddItem(e)}                                   
                                    handleUpdateItem={(e)=>this.handleOnClickUpdate(e, this.state.id)}
                                    isDisabledBtnInsert={this.state.isDisabledBtnInsert}
                                    isDisabledBtnUpdate={this.state.isDisabledBtnUpdate}
                                />
                            <div className="row table-responsive pe-2 ps-2">    
                                <table className="table  table-hover">
                                    <thead>
                                        <tr>
                                            <th className="text-center">ID</th>
                                            <th className="text-center">ItemName</th>
                                            <th className="text-center">Image</th>
                                            <th className="text-center">Price</th>
                                            <th className="text-start">Action</th>
                                        </tr>
                                    </thead>
                                    <tbody className="text-center align-middle">  
                                        {items.length < 1 ? <tr className="text-center"><td colSpan={5}> Data Not Found !</td></tr>:
                                        items.map((item, key) =>{
                                            return(
                                            <ItemList key={key}
                                                id={item.id}
                                                item_name={item.item_name}
                                                image={item.image}
                                                price={this.currencyFormatter(item.price)}
                                                handleEditBy={()=>this.readItemsById(item.id)}
                                                handleDeleteBy={(e)=>this.removeItemsBy(e, item.id)} />
                                        )})}
                                        
                                    </tbody>
                                </table>
                                </div>
                            </div>
                        </div>                            
                    </div>
                }
            </React.Fragment>
        )
    }
}
export default Home;