import React from "react";
import { Link } from "react-router-dom";

class RegisterForm extends React.Component{
    render(){
        return(
            <React.Fragment>
                <div className="page-wrap">
                   <div className="container py-5">
                             <div className="card p-5 border-gray rounded-5 border-1">
                                <form onSubmit={this.props.onSubmit}>
                                    <div className="form-group">
                                       
                                        <input type="text" name="id"
                                            hidden
                                            className="form-control"
                                            value={this.props.id}
                                            onChange={this.props.setId}
                                        />
                                    </div>

                                    <div className="form-group">
                                        <label htmlFor="email" className="text-muted">Email</label>
                                        <input type="email" name="email"
                                            className="form-control border-gray rounded-5 border-1"
                                            placeholder="Enter Email ..."
                                            value={this.props.email}
                                            onChange={this.props.setEmail}
                                        />
                                    </div>

                                    <div className="form-group" hidden>
                                        <label htmlFor="isAdmin" className="text-muted">isAdmin</label>
                                        <input type="text" name="isAdmin"
                                            className="form-control border-gray rounded-5 border-1"
                                            placeholder="Enter Email ..."
                                            value={this.props.isAdmin}
                                            onChange={this.props.setIsAdmin}
                                        />
                                    </div>

                                    <div className="form-group" hidden>
                                        <label htmlFor="avatar" className="text-muted">Avatar</label>
                                        <input type="text" name="text"
                                            className="form-control border-gray rounded-5 border-1"
                                            placeholder="Enter Avatar ..."
                                            value={this.props.avatar}
                                            onChange={this.props.setAvatar}
                                        />
                                    </div>


                                    <div className="form-group">
                                        <label htmlFor="password" className="text-muted">Password</label>
                                        <input type="password" name="password"
                                            className="form-control border-gray rounded-5 border-1"
                                            placeholder="Enter Password"
                                            value={this.props.password}
                                            onChange={this.props.setPassword}
                                        />
                                    </div>
                                    <div className="form-group">
                                        <label htmlFor="confirmPassword" className="text-muted">Confirmation Password</label>
                                        <input type="password" name="confirmPassword"
                                            className="form-control border-gray rounded-5 border-1"
                                            placeholder="Enter Confirmation Password ..."
                                            value={this.props.confirmPassword}
                                            onChange={this.props.setConfirmPassword}
                                        />
                                    </div>

                                    <div className="form-group pt-3">
                                        <button type="submit" className="btn btn-sm btn-primary border-primary rounded-5 border-1">
                                          Register
                                        </button>
                                    </div>
                                    <hr/>
                                    
                                    <div className="form-group">
                                        <label className="ps-0 text-muted">Already Have an Account ?
                                           <Link to={"/react-login/"+ this.props.linkTo}> Login</Link>
                                        </label>
                                    </div>
                                    
                                </form>
                                </div>
                            </div>
                        </div>
                   
            </React.Fragment>
        )
    }
}
export default RegisterForm ;