import React from "react";
class ProfileForm extends React.Component{

 render(){
    return(
        <React.Fragment>
            <div className="page-wrap">
                    <div className="page-center">
                        <div className="card pe-5 ps-5 pb-3 pt-3 border-gray shadow-md rounded-5 border-1">
                            <div className="text-center">
                                <label className="text-muted"> WELCOME BACK {this.props.email} </label>
                            </div> 
                            <hr/>
                                <div className="form-group text-center">                                   
                                    <img src={"https://bedsongultom.com/images/"+this.props.avatar} alt="avatar" id="circle"/>
                                </div>

                                <div className="form-group text-center">                                    
                                    <input type="text" name="email"
                                        className="form-control border-gray text-center rounded-5 border-0 " 
                                        placeholder="Enter Email ..."
                                        value={"ID:" + this.props.id }                                       
                                    />
                                </div>                                
                                <div className="form-group pt-3 text-center">
                                    <button className="btn btn-sm btn-primary border-gray rounded-5 border-1 float-start"
                                        type="submit" onClick={this.props.onClick}> Logout
                                    </button>

                                    <button className="btn btn-sm btn-warning border-gray rounded-5 border-1 float-end text-white"
                                        type="button" onClick={this.props.onClickItems}> Items
                                    </button>

                                </div>


                                <hr/>
                                <div className="text-center">
                                    <label className="text-gray"> Signed-In : {this.props.currentDate} </label>
                                </div> 
                        </div> 
                    </div>
                </div>
        </React.Fragment>

    )
 }


}
export default ProfileForm;