import React from "react";
class ItemForm extends React.Component{
    render(){
        return(
            <React.Fragment> 
                <form onSubmit={this.props.onSubmit}>

                    <div className="col-lg-12">
                        <div className="row">
                            <div className="col-lg-6">    
                                <div className="form-group">
                                    <label htmlFor="item_name" className="text-muted">ID</label>
                                        <input type="number" name="id"
                                            className="form-control"
                                            placeholder="Enter ID ..."
                                            value={this.props.id}
                                            onChange={this.props.setId}
                                        />
                                </div>
                             </div>   
                            <div className="col-lg-6">
                                <div className="form-group">
                                    <label htmlFor="item_name" className="text-muted">Item Name</label>
                                    <input type="item_name" name="item_name"
                                        className="form-control border-gray rounded-2 border-1"
                                        placeholder="Enter item_name ..."
                                        value={this.props.item_name}
                                        onChange={this.props.setItemName}
                                    />
                                </div>
                            </div>    
                            <div className="col-lg-6" hidden>
                                <div className="form-group">
                                    <label htmlFor="image" className="text-muted">Image</label>
                                    <input type="text" name="image"
                                        className="form-control border-gray rounded-2 border-1"
                                        placeholder="Enter image ..."
                                        value={this.props.image}
                                        onChange={this.props.setImage}
                                        
                                    />
                                </div>
                            </div>    

                            <div className="col-lg-6">
                                
                                    <label htmlFor="price" className="text-muted">Price</label>
                                    <div class="input-group mb-3">
                                        <span class="input-group-text" id="basic-addon1">Rp</span>
                                        <input type="number" name="price"
                                            className="form-control border-1"                                            
                                            placeholder="Enter Price"
                                            value={this.props.price}
                                            onChange={this.props.setPrice}
                                        />
                                </div>
                                
                            </div>
                        </div>
                    </div>            
                    
                    <div className="col-lg-12 pt-3">                                        
                        <div className="form-group d-grid gap-2 d-md-flex">
                            <button type="button" className="btn btn-sm btn-primary border-primary rounded-2 border-1"
                                onClick={this.props.handleAddItem}  disabled={this.props.isDisabledBtnInsert}>
                                Insert
                            </button>

                            <button type="button" className="btn btn-sm btn-warning border-warning rounded-2 border-1 text-white"
                                onClick = {(e)=>this.props.handleUpdateItem(e,this.props.id)} disabled={this.props.isDisabledBtnUpdate}>
                                Update
                            </button>
                            </div>
                    </div>
                </form>
                <hr/>
            </React.Fragment>
        )
    }
}
export default ItemForm ;